<template>
<div>
  <CCardBody>
    <CDataTable
      :items="items"
      :fields="fields"
      :items-per-page="perPage"
      column-filter
      hover
      :sorter="sorter"
      @page-change="onPageChange"
      @update:sorter-value="onSorterValue"
      @update:column-filter-value="onColumnFilterValue"
    >
    <template #over-table>
      <CButton class="btn btn-primary" @click="showModal">新增</CButton> 
    </template>
      <template #status="{item}">
        <td>
          <CBadge :color="getBadge(item.status)">
            {{item.status}}
          </CBadge>
        </td>
      </template>


      <template #action="{item, index}">
        <td class="py-2">
          <CButton
            color="primary"
            variant="outline"
            square
            size="sm"
            @click="editModuleCode(item, index)"
          >
            编辑
          </CButton>
          <CButton
            color="primary"
            variant="outline"
            square
            size="sm"
            @click="deleteModuleCode(item, index)"
          >
            删除
          </CButton>
        </td>
      </template>

      <template #under-table="">

    <CPagination
      v-show="pages>1"
      :activePage.sync="activePage"
      :pages="pages"
      @my-page-change="onPageChange"
    />
    </template>

    </CDataTable>
  </CCardBody>



<!----------modal------------->


  <CModal
      title="编辑/新增 业务模块"
      color="primary"
      :show.sync="openModal"
    >
<template>
  <div>
    <CRow>
      <CCol sm="12">
        <CInput
          label="标题"
          placeholder="建议见名知意"
          :value.sync="model.title"
        />
      </CCol>
    </CRow>
    <CRow>
      <CCol sm="12">
        <CInput
          label="常量符号"
          placeholder="建议大写"
          :value.sync="model.name"
        />
      </CCol>
    </CRow>
    <CRow>
      <CCol sm="12">
        <CInput
          label="编号"
          placeholder="不能重复"
          :value.sync="model.code"
        />
      </CCol>
    </CRow>
        <CRow>
      <CCol sm="12">
        <CTextarea
          label="说明"
          placeholder="业务模块说明"
          :value.sync="model.description"
        />
      </CCol>
    </CRow>

  </div>
</template>
<template #footer="">
  <button
                  type="button"
                  class="btn btn-secondary" 
                  @click="hideModal"
                >
                  Cancel
                </button>
                <button
                  type="button"
                  class="btn btn-primary"
                  @click="saveModuleCode"
                >
                  OK
                </button>
</template>
    </CModal>

<!---------------cover--------------------->

<CElementCover  v-show="ajaxDoing"
        :boundaries="[{ sides: ['top', 'left']}]"
        :opacity="0.8"
      >
        <h1 class="d-inline">Loading... </h1><CSpinner size="5xl" color="success"/>
      </CElementCover>


</div>
</template>



<script>

const fields = [
  {key:"id",label:"ID",filter:false},
  {key:"title",label:"标题",sorter:false},
  {key:"code",label:"数字代码"},
  {key:"name",label:"常量表达式",sorter:false},
  {key:"description",label:"说明",sorter:false},
  {key:"status",label:"状态"},
  { 
    key: 'action', 
    label: '操作', 
    sorter: false, 
    filter: false
  }
]



const backendApi=(uri)=>{
  return `${host}${uri}`;
}
import axios from 'axios';

export default {
  name: 'CodeList',
  data () {
    return {
      sorter:{ external: true, resetable: true },
      activePage:1,
      pages:1,
      perPage:5,
      sorterMap:{// 排序

      },
      filterMap:{// 筛选

      },
      ajaxDoing:false,
      openModal:false,
      items:[],
      moduleSelectOptionMap:{},
      typeSelectOptionMap:{},
      model:{
                id:0,
                name:"",
                title:"",
                code:"",
                description:"",
                status:"",
                createTime:"",
                updateTime:""
            },
      fields,
      details: [],
      collapseDuration: 0
    }
  },
  watch:{
    activePage (val) {
      this.onPageChange(val)
    }
  },
  computed:{
  },

  
  mounted: function () {

    this.getList();
},
  methods: {
    hideModal:function(e){
    this.openModal=false;
  },
  showModal(){
this.openModal=true;// 打开modal;
  },
  queryString(map){
    const all ={};
    
    const paginationMap={
      size:this.perPage,
      page:this.activePage
    }
    const tmpSorterMap=this.sorterMap
    const sorterMap ={}
    const arr=[]
    Object.keys(tmpSorterMap).forEach(column=>{
      const value = tmpSorterMap[column]
      value && arr.push(column+" "+value)
    });

    arr.length>0 && (sorterMap["order"]=arr.join(","))

    Object.assign(all,this.filterMap,sorterMap,paginationMap,map)
    return Object.keys(all).map((key) => {
    return encodeURIComponent(key) + '=' + encodeURIComponent(all[key])
}).join('&');
  },
  // 翻页
  onPageChange(page){
    this.activePage=page;
    this.getList();

  },
  onSorterValue(observer){
    const {column,asc}=observer;
    if (column){
      const value =asc===true?"asc":(asc===false?"desc":null)
      if (value){
        this.sorterMap[column]=value
      }else{// 值为null时删除条件
        delete this.sorterMap[column]
      }
    }
    this.getList();

  },
  onColumnFilterValue(observer){
    const component = this
    Object.keys(observer).forEach(column=>{
      component.filterMap[column]=observer[column]
    });
    this.getList();
  },





  getList(){
    const query=this.queryString()
    this.ajaxDoing=true;
    axios.get(`${host}/api/v1/module/list?${query}`).then((response) => {

      const {items,pagination:{size,page,total}}=response.data
        this.items=items
        this.perPage=size,
        this.activePage=page,
        this.pages=Math.ceil(total/size)
        this.ajaxDoing=false;
    });
  },
    saveModuleCode() {
      this.ajaxDoing=true;
      const model = this.model
      const url = model.id?backendApi('/api/v1/module/update'):backendApi('/api/v1/module/create');
      const method = model.id?"put":"post";
      console.log("create",model)
      axios[method](url,model).then((response) => {
        this.ajaxDoing=false;
        this.hideModal();
        location.reload();
      });
      return false;

  },
  editModuleCode(item) {
    this.showModal();
      const id = item.id;
      const url = backendApi(`/api/v1/module/get?id=${id}`)
      axios.get(url).then((response) => {
          this.model = response.data;
          this.ajaxDoing=false;
      });
      return false;
  },
  deleteModuleCode(item) {
    this.ajaxDoing=true;
    const id = item.id;
    const url = backendApi(`/api/v1/module/delete?id=${id}`)
    axios.delete(url).then((response) => {
      this.ajaxDoing=false;
      location.reload();
    });
    return false;
  },
    getBadge (status) {
      switch (status) {
        case 'ON': return 'success'
        case 'OFF': return 'warning'
        default: 'primary'
      }
    }
  }
}
</script>
